  /* Transition CSS */

.page{
  position: absolute;
  top: 75px;
  width: 100%;
}

.fade-appear,
.fade-enter{
  opacity: 0;
  z-index: 1;
}

.fade-appear-active, 
.fade-enter.fade-enter-active{
  opacity: 1;
  transition: opacity 300ms linear 600ms;
}

.fade-exit{
  opacity: 1;
}

.fade-exit.fade-exit-active{
  opacity: 0;
  transition: opacity 300ms linear;
}


/* Gallery CSS  */

.row {
  display: -ms-flexbox; /* IE10 */
  display: flex;
  -ms-flex-wrap: wrap; /* IE10 */
  flex-wrap: wrap;
  padding: 0 4px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

/* Create four equal columns that sits next to each other */
.column {
  // -ms-flex: 24%; /* IE10 */
  // flex: 24%;
  // max-width: 24%;
  // padding: 0 20px;
  -ms-flex: 33.5%;
  flex: 33.5%;
  max-width: 33.5%;
    padding: 0 15px;

}

.column img {
  margin-top: 8px;
  vertical-align: middle;
  width: 100%;
  margin-bottom: 2rem;
  border-radius: 5px;
  height: 640px;
  object-fit: cover;
}

.lastrow{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 2rem;
}

@media screen and (max-width: 992px) {
  .column {
    -ms-flex: 48%;
    flex: 48%;
    max-width: 48%;
  }

  .App{
    overflow-x: hidden;
    overflow-y: hidden;
  }
}

@media screen and (max-width: 600px) {
  .column {
    -ms-flex: 95%;
    flex: 95%;
    max-width: 95%;
  }
  .App{
    overflow-x: hidden;
    overflow-y: hidden;
  }
}

.imagehov{
  transition: transform .4s; /* Animation */
}

.imagehov:hover{
  transform: scale(1.1); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}


// Navbar


.edit-navbar{
  position: absolute;
