}





/* Overlay */

.overlay {
  width: 100%;
  height: 100vh;
  top: -100vh;
  position: absolute;
  z-index: 7;
  background-color: $darkColor;
  overflow-y: hidden;
  overflow-x: hidden;
  transition: 0.7s;
  display: flex;
  align-items: center;
  animation-duration: 0.7s;
  animation-fill-mode: forwards;
  will-change: top;
  backface-visibility: hidden;
  perspective: 1000;
}




.overlay-content {
  position: absolute;
  width: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.overlay a {
  padding: 5.5vh;
  text-decoration: none;
  font-size: 20px;
  font-weight: 300;
  color: #b6b6b6;
  display: block;
  transition: 0.3s;
}

.hover-underline{
  color: #cacaca;
  text-decoration: none;
}

.hover-underline:after {
  display:block;
  content: '';
  border-bottom: solid 3px #cacaca;  
  transform: scaleX(0);  
  transition: transform 250ms ease-in-out;
}

.hover-underline:hover:after { transform: scaleX(1); }


.overlay .closebtn {
  position: absolute;
  top: 20px;
  right: 45px;
  font-size: 45px;
}



@media screen and (max-height: 600px) {
  .overlay {overflow-y: auto;}
  .overlay a {font-size: 20px}
  .overlay .closebtn {
  font-size: 40px;
  top: 15px;
  right: 35px;
  }

  .pricingro{
    flex-direction: column;
  }
}






